.home-content {
  align-items: flex-start;
  color: white;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  font-family: "IBM Plex Sans", sans-serif;
  justify-content: center;
  margin: 1rem; }
  .home-content ul {
    margin: 1rem 0;
    padding: 0;
    list-style: none; }
  .home-content li {
    margin: 0.5rem 0; }

.home-content-section {
  margin: 1rem; }

.home-link {
  color: white;
  font-size: 22px;
  font-weight: bold;
  margin: 0 0.7rem 0 0;
  padding: 0;
  transition: 0.3s;
  text-decoration: none; }
  .home-link:hover {
    color: #ffe170;
    background-color: rgba(255, 255, 255, 0.2); }

.home-logo-link {
  border-bottom: 3px solid white;
  color: white;
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 30px;
  font-weight: bold;
  letter-spacing: 4px;
  margin-right: 1rem;
  transition: 0.3s;
  text-decoration: none; }
  .home-logo-link:hover {
    color: #ffe170;
    background-color: rgba(255, 255, 255, 0.2);
    border-bottom: 3px solid #ffe170; }

.home-icon {
  color: white;
  font-size: 26px;
  margin-right: 0.8rem;
  transition: 0.3s; }
  .home-icon:hover {
    color: #ffe170; }
