@import url("https://fonts.googleapis.com/css?family=IBM+Plex+Sans&display=swap");
.register-btn {
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 1rem;
  border-width: 0;
  box-shadow: 0 0 0.1rem 0.001rem #999;
  color: black;
  font-size: 14px;
  letter-spacing: 2px;
  padding: 10px 16px;
  text-decoration: none;
  transition: 0.3s; }
  .register-btn:hover {
    background-color: white;
    box-shadow: 0 0 0.3rem 0.001rem #999;
    color: #880e4f; }
  .register-btn:visited {
    color: black; }
    .register-btn:visited:hover {
      color: #880e4f; }

.login-btn {
  background-color: rgba(255, 255, 255, 0);
  border-color: rgba(255, 255, 255, 0.85);
  border-radius: 1rem;
  border-style: solid;
  border-width: 2px;
  color: rgba(255, 255, 255, 0.85);
  font-size: 14px;
  letter-spacing: 2px;
  padding: 8px 14px;
  text-decoration: none;
  transition: 0.3s; }
  .login-btn:hover {
    background-color: rgba(255, 255, 255, 0.2); }
  .login-btn:visited {
    color: rgba(255, 255, 255, 0.85); }

.primary-btn {
  background-color: #880e4f;
  border-radius: 0.4rem;
  border-width: 0px;
  box-shadow: 0 0 0.1rem 0.0001rem #999;
  color: #f0f0f0;
  letter-spacing: 3px;
  margin: 20px 10px;
  padding: 12px 22px;
  text-decoration: none;
  transition: 0.3s; }
  .primary-btn:hover {
    background-color: #bc477b;
    box-shadow: 0 0 0.3rem 0.01rem #999; }
  .primary-btn:visited {
    color: #f0f0f0; }

.clear-btn {
  background-color: rgba(0, 0, 0, 0.5);
  border-width: 0;
  border-color: rgba(0, 0, 0, 0);
  color: white;
  cursor: pointer;
  margin: 0;
  padding: 0; }

