#tag-list {
  align-items: flex-start;
  display: flex;
  flex-wrap: nowrap;
  overflow-x: auto;
  margin: 0.8rem 0 1.5rem 0;
  padding: 0;
  list-style: none;
  width: 100%; }
  #tag-list li {
    color: white;
    background-color: #141a30;
    border-radius: 6px;
    padding: 0.1rem 0.6rem;
    margin: 0 0.2rem;
    transition: 500ms ease; }
    #tag-list li:hover {
      background-color: #384b8c; }
